import React from 'react';

/* Design Import */
import './Solving.scss';

/* Icon Import */

/* Material Import */

/* Custom Import */
import { getData, postData } from '../../common'
import {SimpleSelect} from "../../components";

class Solving extends React.Component {
    /* State */
    state = {
        problemData: {},
        selectedLanguage: '',
        code: ''
    }

    /* Components */

    /* Method */
    componentDidMount() {
        this.getProblemData();
        this.getDefaultCodeForm();
    }

    getProblemData = () => {
        const { match } = this.props
        const problemCode = match.params.problem_code;
        let resData = {};

        getData(`/api/problem/${problemCode}`, (res) => {
            const data = res.data.success;

            let parsed = JSON.parse(data.description);

            data.description = parsed.text;
            data.answer = parsed.answer !== 'undefined' ? parsed.answer : '';
            data.testCases = this.getTestCases(problemCode);
            data.choices = parsed.choices !== 'undefined' ? parsed.choices : [];

            resData = data;
        }, (err) => {

        });
        this.setState({ problemData: resData });
    };

    getTestCases = (problemCode) => {
        let data = [];
        getData(`/api/problem/${problemCode}/io`, (res) => {
            data = res.data.success;
        }, (err) => {

        });
        return data;
    }

    getLanguageTypes = () => {
        let items = [
            { label: 'Python', value: 'python' }
        ];
        return items;
    }

    getDefaultCodeForm = () => {
        const { match } = this.props;
        getData(` /api/problem/${match.params.problem_code}/signature/${this.state.selectedLanguage}`, (res) => {
            this.setState({ code: res.data.success });
        }, (err) => {
            this.setState({ code: '' });
        });
    }

    onChangeCode = (event) => {
        this.setState({ code: event.target.value });
    }

    printSolvingForm = () => {
        switch(this.state.problemData.type) {
            case 'programming': //  
                return this.printProgrammingSolvingForm();
            case 'short': //  
                return this.printShortSolvingForm();
            case 'multiple': //  
                return this.printMultipleSolvingForm();
            default:
                return null;
        }
    };

    printProgrammingSolvingForm = () => {
        const items = this.getLanguageTypes();
        const pData = this.state.problemData;

        return (
            <div className="page_wrap layout solving">
                <div className="box problem_title flex-align middle">
                    <em>{ pData.name }</em>
                    <SimpleSelect
                        title={'Language'}
                        items={items}
                        start={this.state.selectedLanguage}
                        changeEvent={(event) => {
                            this.setState({ selectedLanguage: event.target.value });
                        }}
                    />
                </div>
                <div className="box problem_desc">
                    <em className="title"> </em>
                    <em className="description">
                        { pData.description }
                    </em>
                </div>
                <div className="box code_title flex-align middle"><em>Solution</em></div>
                <div className="box code">
                    <textarea
                        placeholder={'Code...'}
                        value={this.state.code}
                        onChange={this.onChangeCode}
                    />
                </div>
                <div className="box execute_title flex-align middle"><em>Execute Result</em></div>
                <div className="box result">
                    <textarea placeholder={'Result Log...'} readOnly={true}>

                    </textarea>
                </div>
                <div className="box buttons flex-align middle">
                    <button className="design_btn_A">  </button>
                </div>
            </div>
        );
    };

    printShortSolvingForm = () => {
        return (
            <div className="page_wrap layout solving">

            </div>
        );
    };

    printMultipleSolvingForm = () => {
        return (
            <div className="page_wrap layout solving">

            </div>
        );
    };

    render() {
        return this.printSolvingForm();
    };
}

export default Solving;
